import React from 'react';
import { BrowserRouter as Router, Switch, Route, Redirect } from 'react-router-dom';
import Home from '../Home';



function App() {
    return (
        <Router>
            <Switch>
                <Route exact path='/avatar' component={Home} />
                <Route path='**' render={() => <Redirect to='/avatar/?lang=en' />} />
            </Switch>
        </Router>
    );
}

export default App;
