import React, { useState } from 'react';
import styles from './tabs.module.scss';
import { Button, Form, FormGroup, Label, Input, Row, Col } from 'reactstrap';
import sendRequest from '../../utils/sendRequest';
import Swal from 'sweetalert';
import { getPartner_en } from '../../utils/english';
import { getPartner_ar } from '../../utils/arabic';

function Companies() {
    const [companyData, setData] = useState({});
    const [submit, setSubmit] = useState(false);
    const urlParams = new URLSearchParams(window.location.search);
    const lang = urlParams.get('lang');
    const company =  lang === 'ar'
        ? getPartner_ar()
        : getPartner_en();
    const addData = (e) => {
        setData({ ...companyData, [e.target.name]: e.target.value });
    };
    const sendData = async (e) => {
        e.preventDefault();
        setSubmit(true);
        const res = await sendRequest('signup', companyData, { 'user-type': 'partner' });
        if (res.status === 200) {
            document.getElementById('companyForm').reset();
            setSubmit(false);
            Swal('Good job!', res.data.message, 'success');            
        } else {
            setSubmit(false);
            Swal('Oops!', res.message, 'error');
        }
    };
    return (
        <section className={`${styles.form} py-5 px-5`}>
            <Form onSubmit={sendData} id='companyForm'>
                <Row>
                    <Col xs="12" md="12">
                        <h2 className="text-capitalize mb-5 text-dark">{company.title}</h2>
                        <p>{company.description}</p>
                    </Col>
                    <Col xs="12" md="6">
                        <FormGroup>
                            <Label className="text-capitalize">{company.label_firstName}</Label>
                            <Input type="text" placeholder={company.placeholder_firstName} name='firstName' onChange={addData} />
                        </FormGroup>
                    </Col>
                    <Col xs="12" md="6">
                        <FormGroup>
                            <Label className="text-capitalize">{company.label_lastName}</Label>
                            <Input type="text" placeholder={company.placeholder_lastName} name='lastName' onChange={addData} />
                        </FormGroup>
                    </Col>
                    <Col xs="12" md="12">
                        <FormGroup>
                            <Label className="text-capitalize">{company.label_email}</Label>
                            <Input type="email" placeholder={company.placeholder_email} name='email' onChange={addData} />
                        </FormGroup>
                    </Col>
                    <Col xs="12" md="12">
                        <FormGroup>
                            <Label className="text-capitalize">{company.label_phoneNumber}</Label>
                            <Input type="tel" placeholder={company.placeholder_phoneNumber} name='phone' onChange={addData} />
                        </FormGroup>
                    </Col>
                    <Col xs="12" md="6">
                        <FormGroup>
                            <Label className="text-capitalize">{company.label_company}</Label>
                            <Input type="text" placeholder={company.placeholder_company} name='company' onChange={addData} />
                        </FormGroup>
                    </Col>
                    <Col xs="12" md="6">
                        <FormGroup>
                            <Label className="text-capitalize">{company.label_position}</Label>
                            <Input type="text" placeholder={company.placeholder_position} name='jobTitle' onChange={addData} />
                        </FormGroup>
                    </Col>
                    <Col xs="12" md="12">
                        <Button className="text-uppercase w-100" color="primary" type='submit' disabled={submit}>
                            {company.button}
                        </Button>
                    </Col>
                </Row>
            </Form>
        </section>
    );
}

export default Companies;
