import React, { useState } from 'react';
import styles from './tabs.module.scss';
import { Button, Form, FormGroup, Label, Input, Row, Col } from 'reactstrap';
import sendRequest from '../../utils/sendRequest';
import Swal from 'sweetalert';

import { getIndividual_en } from '../../utils/english';
import { getIndividual_ar } from '../../utils/arabic';

function Individuals() {
    const [individualData, setData] = useState({});
    const [submit, setSubmit] = useState(false);
    const urlParams = new URLSearchParams(window.location.search);
    const lang = urlParams.get('lang');
    const individual =  lang === 'ar'
        ? getIndividual_ar()
        : getIndividual_en();
    const addData = (e) => {
        setData({ ...individualData, [e.target.name]: e.target.value });
    };
    const sendData = async (e) => {
        e.preventDefault();
        setSubmit(true);
        const res = await sendRequest('signup', individualData, { 'user-type': 'individual' });
        if (res.status === 200) {
            document.getElementById('individualForm').reset();
            setSubmit(false);
            Swal('Good job!', res.data.message, 'success');            
        } else {
            setSubmit(false);
            Swal('Oops!', res.message, 'error');
        }
    };
    return (
        <section className={`${styles.form} py-5 px-5`}>
            <Form onSubmit={sendData} id='individualForm'>
                <Row>
                    <Col xs="12" md="12">
                        <h2 className="text-capitalize mb-5 text-dark">{ individual.title } </h2>
                        <p>{individual.description}</p>
                    </Col>
                    <Col xs="12" md="6">
                        <FormGroup>
                            <Label className="text-capitalize">{individual.label_firstName}</Label>
                            <Input type="text" placeholder={individual.placeholder_firstName} name='firstName' onChange={addData} required />
                        </FormGroup>
                    </Col>
                    <Col xs="12" md="6">
                        <FormGroup>
                            <Label className="text-capitalize">{individual.label_lastName}</Label>
                            <Input type="text" placeholder={individual.placeholder_lastName} name='lastName' onChange={addData} required />
                        </FormGroup>
                    </Col>
                    <Col xs="12" md="12">
                        <FormGroup>
                            <Label className="text-capitalize">{individual.label_email}</Label>
                            <Input type="email" placeholder={individual.placeholder_email} name='email' onChange={addData} required />
                        </FormGroup>
                    </Col>
                    <Col xs="12" md="12">
                        <FormGroup>
                            <Label className="text-capitalize">{individual.label_phoneNumber}</Label>
                            <Input type="tel" placeholder={individual.placeholder_phoneNumber} name='phone' onChange={addData} required />
                        </FormGroup>
                    </Col>
                    <Col xs="12" md="12">
                        <Button className="text-uppercase w-100" color="primary" type='submit' disabled={submit}>
                            {individual.button}
                        </Button>
                    </Col>
                </Row>
            </Form>
        </section>
    );
}

export default Individuals;
