import React from 'react';
import styles from './info.module.scss';
import { getContactInfo_en } from '../../utils/english';
import { getContactInfo_ar } from '../../utils/arabic';

function Info() {
    const urlParams = new URLSearchParams(window.location.search);
    const lang = urlParams.get('lang');
    const info = lang === 'ar'
        ? getContactInfo_ar()
        : getContactInfo_en();

    return (
        <section className={`${styles.info}`}>
            <div className={`${styles.contactText} text-white`}>
                <h2 className="text-uppercase mb-4">{info.title}</h2>
                <p>
                    {info.description}
                </p>
            </div>
            <div className={`${styles.contactInfo} text-white my-5`}>
                <p>
                    Email: <span className="px-4">{info.email}</span>
                </p>
                <p>
                    Phone: <span className="px-4">{info.phone}</span>
                </p>
            </div>
        </section>
    );
}

export default Info;
