import React, { useEffect } from 'react';
import { Button } from 'reactstrap';
import styles from './application.module.scss';
import getStart from '../../utils/getStart';

function Application(headerData) {
    useEffect(() => {
        document.getElementById('appDesc').innerHTML = headerData.appDesc;
    });
    return (
        <section className={`${styles.application} py-5`}>
            <div className="row align-items-center">
                <div className="col-sm-12 col-md-7 mb-4">
                    <h2 className="text-capitalize text-dark mb-4">{headerData.appName}</h2>
                    <p className=" mb-4 text-dark" id='appDesc'></p>
                    <Button className="text-uppercase" color="primary" onClick={getStart}>{headerData.button}</Button>
                </div>
                <div className="col-sm-12 col-md-5">
                    <img className="img-fluid" src={headerData.appImg} alt={headerData.appImg} />
                </div>
            </div>
        </section>
    );
}

export default Application;
