/* eslint-disable no-undef */
import React, { useState } from 'react';
import styles from './navbar.module.scss';
import {
    Collapse,
    Navbar,
    NavbarToggler,
    NavbarBrand,
    Nav,
    NavItem,
    NavLink,
    Button,
} from 'reactstrap';

import { getNav_en } from '../../utils/english';
import { getNav_ar } from '../../utils/arabic';
import getStart from '../../utils/getStart';

function BrandNavbar() {
    const goToBottom = () => {
        window.scrollTo({
            top: document.body.scrollHeight,
            behavior: 'smooth'
        });
    };
    const [isOpen, setIsOpen] = useState(false);
    const urlParams = new URLSearchParams(window.location.search);
    const lang = urlParams.get('lang');
    const navData = lang === 'ar'
        ? getNav_ar()
        : getNav_en();
    const toggle = () => setIsOpen(!isOpen);
    return (
        <div className={`${styles.nav}`}>
            <Navbar expand="md" light className="py-4 px-0">
                <NavbarBrand href="/">
                    <img src={navData.logo} alt="logo" id={styles.logo} />
                    avatar
                </NavbarBrand>
                <NavbarToggler onClick={toggle} />
                <Collapse isOpen={isOpen} navbar>
                    <Nav className={`${styles.navItem} ml-auto`} navbar>
                        <NavItem>
                            <NavLink href="#" onClick={goToBottom} className="text-uppercase text-primary">
                                {navData.contactUs}
                            </NavLink>
                        </NavItem>
                        <NavItem>
                            <NavLink href={navData.path} className="text-uppercase">
                                <img src={navData.lang} alt="lang" />
                            </NavLink>
                        </NavItem>
                        <NavItem className="pr-0">
                            <NavLink href="#" className="text-uppercase">
                                <Button outline color="primary" className="px-4 py-2" onClick={getStart}>
                                    {navData.getStart}
                                </Button>
                            </NavLink>
                        </NavItem>
                    </Nav>
                </Collapse>
            </Navbar>
        </div>
    );
}

export default BrandNavbar;
