import React, { useState } from 'react';
import Swal from 'sweetalert';

import styles from './form.module.scss';
import { Button, Form, FormGroup, Label, Input } from 'reactstrap';
import sendRequest from '../../utils/sendRequest';
import { getContactForm_en } from '../../utils/english';
import { getContactForm_ar } from '../../utils/arabic';

function ContactForm() {
    const [contactData, setData] = useState({});
    const [submit, setSubmit] = useState(false);
    const urlParams = new URLSearchParams(window.location.search);
    const lang = urlParams.get('lang');
    const info = lang === 'ar'
        ? getContactForm_ar()
        : getContactForm_en();
        

    const addData = (e) => {
        setData({ ...contactData, [e.target.name]: e.target.value });
    };
    const sendData = async (e) => {
        e.preventDefault();
        setSubmit(true);
        const res = await sendRequest('contact', contactData,{});
        if (res.status === 200) {
            document.getElementById('individualForm').reset();
            setSubmit(false);
            Swal('Good job!', res.data.message, 'success');            
        } else {
            setSubmit(false);
            Swal('Oops!', res.message, 'error');
        }
    };
    return (
        <section className={`${styles.contactForm}`}>
            <Form onSubmit={sendData}>
                <FormGroup>
                    <Label className="text-capitalize text-white">{info.label_name}</Label>
                    <Input
                        className="bg-primary border-0 py-4 rounded-0 text-white"
                        type="text"
                        placeholder={info.placeholder_name}
                        name="name"
                        required
                        onChange={addData}
                    />
                </FormGroup>

                <FormGroup>
                    <Label className="text-capitalize text-white">{info.label_email}</Label>
                    <Input
                        className="bg-primary border-0 py-4 rounded-0 text-white"
                        type="email"
                        placeholder={info.placeholder_email}
                        name="email"
                        required
                        onChange={addData}
                    />
                </FormGroup>

                <FormGroup>
                    <Label className="text-capitalize text-white">{info.label_message}</Label>
                    <Input
                        className="bg-primary border-0 pt-4 rounded-0 text-white"
                        type="textarea"
                        placeholder={info.placeholder_message}
                        name="message"
                        id="exampleText"
                        onChange={addData}
                    />
                </FormGroup>

                <Button
                    color="dark"
                    className="border-0 rounded-0 py-3 px-5 text-uppercase font-weight-bold"
                    disabled={submit}
                >
                    {info.button}
                </Button>
            </Form>
        </section>
    );
}

export default ContactForm;
